	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Three Monkeys","date":"11 Feb 2002","slug":"/2002-02-11-three-monkeys/","tags":"Y2002"},"html":"<p>All three crises are still bubbling.<br />\nThe number of informants in the <b>NPD scandal</b> is up to 5, maybe 6, and there are more and more calls from FDP and CDU to drop the case.<br />\nMeanwhile, in the <b>job placement scandal</b>, reports are that Arbeitsamt head Jagoda was informed of irregularites 4 years ago, and other reports that he ordered his investigators to refute the current charges, rather than to see if they were true.  He's not a political appointee, but he's probably on the way out.<br />\nAnd in Brussels, Schröder may have avoided a formal <b>EU budget warning</b>, but at the cost of a lot of bad blood in the Commission and the smaller member countries.<br />\nI can't remember that Kohl ever had to juggle 3 crises at once. Hell, Kohl couldn't juggle at all.</p>"},"next":{"fields":{"title":"Schill To Take Drug Test","date":"09 Feb 2002","slug":"/2002-02-09-schill-to-take-drug-test/"}},"previous":{"fields":{"title":"Tweedle Dum & Tweedle Dee","date":"11 Feb 2002","slug":"/2002-02-11-tweedle-dum-tweedle-dee/"}}},{"node":{"fields":{"title":"Schill To Take Drug Test","date":"09 Feb 2002","slug":"/2002-02-09-schill-to-take-drug-test/","tags":"Y2002"},"html":"<p>FAZ <a href=\"http://www.faz.com/IN/INtemplates/eFAZ/docmain.asp?rub={B1311FCC-FBFB-11D2-B228-00105A9CAF88}&doc={1B9ED4C2-B8A7-412D-9ACB-3A325414D261}\">Interior Minister to Undergo Drug Test</a>. Hamburg's 'Judge Merciless' makes national headlines again, but now on the defensive. Celebrity parties where cocaine was seen, bodyguards with Mafia ties, and now a sworn statement that he rubbed 'white powder' on his gums. He calls the TV report a 'pig magazine' and swears to have his hair analyzed. Shades of Christoph Daum, and Schill has the same wild look in his eyes.</p>"},"next":{"fields":{"title":"40","date":"09 Feb 2002","slug":"/2002-02-09-40/"}},"previous":{"fields":{"title":"Three Monkeys","date":"11 Feb 2002","slug":"/2002-02-11-three-monkeys/"}}},{"node":{"fields":{"title":"40","date":"09 Feb 2002","slug":"/2002-02-09-40/","tags":"Y2002"},"html":"<p><a href=\"http://andrea.editthispage.com/2002/02/09\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2002/02/shrekscott.jpg\" height=\"68\" width=\"50\" border=\"0\" alt=\"shrekscott.jpg: \" align=\"left\" /></a>I woke up this morning and feel pretty good. I haven't turned into a troll or an ogre, not yet. <b>Update</b> Hold on, I <a href=\"http://andrea.editthispage.com/2002/02/09\">have</a> turned into an ogre after all. Well, if I'm an ogre, that makes Christopher a donkey!</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2002/02/olymears.jpg\" height=\"60\" width=\"50\" border=\"0\" alt=\"olymears.jpg: \" align=\"right\" />We watched the replay of the Olympic Opening Ceremony at 9am this morning. The German white stocking caps with the long earlappers looked silly, I thought. The Olympics are crassly commercial and nationalistic, of course, but there are far worse ways to vent off such things. Like <a href=\"http://hal.editthispage.com/2002/02/08\">Hal</a>, I enjoyed the oversized animal puppets. Seeing the US hockey team light the flame reminded me of February 1980, when they won the gold medal, and I had just started seeing a German exchange student in the next town.</p>"},"next":{"fields":{"title":"I Need A Hug","date":"08 Feb 2002","slug":"/2002-02-08-i-need-a-hug/"}},"previous":{"fields":{"title":"Schill To Take Drug Test","date":"09 Feb 2002","slug":"/2002-02-09-schill-to-take-drug-test/"}}}],"pathPrefix":"","first":false,"last":false,"index":929,"pageCount":1101}}

